@import "../../style/variables.scss";

.caform {
    width: 280px;
    padding: 20px;
    // margin-top: 50px;
    background-color: #fff;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.25);
    &__title {
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;
        color: rgba(0, 0, 0, 0.7);
        text-align: center;
    }
    label {
        display: block;
        margin-top: 15px;
        font-weight: 600;
        font-size: 13px;
        line-height: 18px;
        color: rgba(0, 0, 0, 0.7);
        span {
            color: $cancel;
        }
    }
    input {
        width: 100%;
        margin-top: 5px;
        height: 24px;
        background: $inputs;
        border: 1px solid rgba(0, 0, 0, 0.15);
        padding: 10px;
        font-weight: 600;
        font-size: 12px;
        line-height: 16px;
        color: rgba(0, 0, 0, 0.5);
    }
    button {
        width: 150px;
        height: 40px;
        display: block;
        margin: 20px auto 0 auto;
        border: 1px solid rgba(0, 0, 0, 0.4);
        border-radius: 24px;
        cursor: pointer;
        background-color: #fff;
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;
        color: rgba(0, 0, 0, 0.7);
        transition: 0.3s all;
        &:hover {
            background-color: $inputs;
        }
        &:disabled {
            background-color: $disabled;
        }
    }
}
