@import "./variables";
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&family=Roboto&display=swap");

* {
    font-family: "Open Sans", sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul,
li,
dl {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

a {
    text-decoration: none;
    color: inherit;
    display: inline-block;
    &:hover {
        text-decoration: none;
        color: inherit;
    }
}

p {
    margin: 0;
    padding: 0;
}

html {
    background: linear-gradient(65.02deg, #8088f3 0.85%, #9f33e7 99.7%);
}

body {
    position: relative;
}

.no-data {
    font-weight: 700;
    font-size: 20px;
    line-height: 27px;
    color: rgba(0, 0, 0, 0.5);
    text-align: center;
}
